import { Buffer } from "buffer";

import BANKS from "@/constants/banks";

export function getBankBIN(bankShortName) {
  let bin = "";
  for (let bank of BANKS) {
    if (
      bank?.bankShortName.toLocaleLowerCase() ===
      bankShortName.toLocaleLowerCase()
    ) {
      bin = bank.bankId;
      break;
    }
  }
  return bin;
}

export function generateCheckSum(text) {
  let crc = 0xffff; // initial value
  let polynomial = 0x1021; // 0001 0000 0010 0001  (0, 5, 12)
  let bytes = Buffer.from(text, "utf-8");
  for (let b of bytes) {
    for (let i = 0; i < 8; i++) {
      let bit = ((b >> (7 - i)) & 1) == 1;
      let c15 = ((crc >> 15) & 1) == 1;
      crc <<= 1;
      if (c15 ^ bit) crc ^= polynomial;
    }
  }
  return (crc & 0xffff).toString(16);
}

export function buildQrCodeContent({ bankId, accountNo, amount, message }) {
  let part12Builder = `00${bankId.length
    .toString()
    .padStart(2, "0")}${bankId}01${accountNo.length
    .toString()
    .padStart(2, "0")}${accountNo}`;
  let part11Builder = `0010A00000072701${part12Builder.length
    .toString()
    .padStart(2, "0")}${part12Builder}0208QRIBFTTA`;
  let part1Builder = `38${part11Builder.length
    .toString()
    .padStart(2, "0")}${part11Builder}`;
  let part21Builder = `08${message.length
    .toString()
    .padStart(2, "0")}${message}`;
  let part2 = `530370454${amount.length
    .toString()
    .padStart(2, "0")}${amount}5802VN62${part21Builder.length
    .toString()
    .padStart(2, "0")}${part21Builder}`;

  let builder = `000201010212${part1Builder}${part2}6304`;

  let qrCode = builder + generateCheckSum(builder).toUpperCase();
  return qrCode;
}

export function removeAccents(str) {
  const accents = [
    /[\300-\306]/g,
    /[\340-\346]/g, // A, a
    /[\310-\313]/g,
    /[\350-\353]/g, // E, e
    /[\314-\317]/g,
    /[\354-\357]/g, // I, i
    /[\322-\330]/g,
    /[\362-\370]/g, // O, o
    /[\331-\334]/g,
    /[\371-\374]/g, // U, u
    /[\321]/g,
    /[\361]/g, // N, n
    /[\307]/g,
    /[\347]/g, // C, c
    /[\320]/g,
    /[\360]/g, // D, d
    /[\335]/g,
    /[\375-\377]/g, // Y, y
  ];
  const noAccents = [
    "A",
    "a",
    "E",
    "e",
    "I",
    "i",
    "O",
    "o",
    "U",
    "u",
    "N",
    "n",
    "C",
    "c",
    "D",
    "d",
    "Y",
    "y",
  ];

  for (let i = 0; i < accents.length; i++) {
    str = str.replace(accents[i], noAccents[i]);
  }

  return str;
}
