<template>
  <div class="container">
    <!-- Bảng danh sách employees -->
    <div class="emp-list">
      <div class="summary">
        <h2>Lương nhân sự tháng 5 năm 2025</h2>
        <p><strong>Tổng số tiền: {{ formatAmount(totalAmount) }}</strong></p>
        <p v-for="(deptAmount, dept) in deptAmounts" :key="dept">
          <strong>{{ dept }}:</strong> {{ formatAmount(deptAmount) }}
        </p>
      </div>
      <h2>Danh sách nhân sự</h2>
      <table class="table">
        <thead>
          <tr>
            <th style="width: 20px;">STT</th>
            <th>Bộ phận</th>
            <th>Tên</th>
            <th style="width: 250px;">Số tiền
              <button class="toggle-button" @click.stop="toggleAmountVisibilityAll">
                {{ showAmountAll ? "Ẩn tất cả" : "Hiện tất cả" }}
              </button>
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(emp, ind) in employees" :key="emp.name" @click="selectEmployee(emp, ind)" :class="[
            (selectedEmployee && emp.id === selectedEmployee.id) ? 'emp-row emp-row-selected' : 'emp-row',
            isEmployeeModified(emp) ? 'emp-modified' : ''
          ]">
            <td>{{ ind + 1 }}</td>
            <td>{{ emp.dept }}</td>
            <td>{{ emp.name }}</td>
            <td>
              {{ showAmount[emp.id] ? formatAmount(emp.amount) : "*** VNĐ" }}
              <button class="toggle-button" @click.stop="toggleAmountVisibility(emp)">
                {{ showAmount[emp.id] ? "Ẩn" : "Hiện" }}
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Khu vực chi tiết của emp được chọn -->
    <div class="emp-detail">
      <h2>Chi tiết nhân sự</h2>
      <div v-if="selectedEmployee">
        <div style="text-align: left; color: orange;">
          <label v-if="selectedEmployee.isModified"><strong>Bạn đã thay đổi dữ liệu này so với bản gốc</strong> <button
              @click="resetChanges" class="reset-button">
              Reset
            </button></label>
        </div>
        <br>
        <div class="detail-field">
          <label><strong>Tên nhân sự:</strong></label>
          <label>{{ selectedEmployee.name }}</label>
        </div>

        <div class="detail-field">
          <label><strong>Tên ngân hàng:</strong></label>
          <select v-model="selectedEmployee.bankCode" class="select">
            <option v-for="bank in banks" :key="bank.bankCode" :value="bank.bankCode">
              {{ bank.name }}
            </option>
          </select>
        </div>

        <div class="detail-field">
          <label><strong>Số tài khoản:</strong></label>
          <input type="text" v-model="selectedEmployee.bankAccount" class="" />
        </div>

        <div class="detail-field">
          <label><strong>Số tiền:</strong></label>
          <button class="toggle-button" @click.stop="toggleAmountVisibility(selectedEmployee)">
            {{
              showAmount[selectedEmployee.id]
                ? "Ẩn"
                : "Hiện"
            }}
          </button>
          <input v-show="showAmount[selectedEmployee.id]" type="text" v-model="formattedAmount" 
            class="input" />
        </div>

        <div class="detail-field">
          <label><strong>Nội dung chuyển tiền:</strong></label>
          <textarea v-model="selectedEmployee.message" rows="4" cols="50" class="" />
        </div>

        <!-- Nút tạo QR Code -->
        <button @click="generateQRCode" class="generate-button">
          Update QR Code
        </button>
        <!-- Hiển thị QR Code -->
        <div v-if="qrcodeContent" class="qrcode-container">
          <canvas id="qrcode"></canvas>
        </div>
      </div>
      <div v-else>
        <p>Vui lòng chọn một nhân sự từ bảng.</p>
      </div>
    </div>
  </div>
</template>

<script>
import QRCode from "qrcode";
import BANKS from "@/constants/banks.json";
import EMPLOYEES from "@/constants/202505.json";
import { buildQrCodeContent, removeAccents } from '@/utils/bank-qr';

export default {
  data() {
    return {
      showAmountAll: false,
      showAmount: {},
      selectedEmployee: null,
      originalData: {},
      selectedEmpIndex: -1,
      qrcodeContent: "",
      banks: BANKS,
      employees: [],
      key: ""
    };
  },
  computed: {
    totalAmount() {
      return this.employees.reduce((sum, emp) => sum + parseInt(emp.amount), 0);
    },
    // Tính số tiền của từng bộ phận
    deptAmounts() {
      return this.employees.reduce((amounts, emp) => {
        amounts[emp.dept] = (amounts[emp.dept] || 0) + parseInt(emp.amount);
        return amounts;
      }, {});
    },
    formattedAmount: {
      get() {
        return this.formatAmount((this.selectedEmployee?.amount || "") + '');
      },
      set(value) {
        this.selectedEmployee.amount = value.replace(/\./g, '').replace(',', '.').replace(/[^\d.]/g, '');
      },
    },
  },
  methods: {
    formatAmount(amount) {
      const formatted = amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
      return formatted.endsWith("VNĐ") ? formatted : formatted + " VNĐ";
    },
    toggleAmountVisibilityAll() {
      this.showAmountAll = !this.showAmountAll;
      this.employees.forEach(emp => {
        this.showAmount[emp.id] = this.showAmountAll;
      });
    },
    toggleAmountVisibility(emp) {
      this.showAmount[emp.id] =
        !this.showAmount[emp.id];
    },
    selectEmployee(emp, ind) {
      this.selectedEmpIndex = ind
      this.selectedEmployee = { ...emp };
      this.generateQRCode();
    },
    isEmployeeModified(emp) {
      return JSON.stringify(this.originalData[emp.id]) !== JSON.stringify(emp);
    },
    resetChanges() {
      if (this.selectedEmpIndex != -1) {
        this.selectedEmployee = { ...this.originalData[this.selectedEmployee.id] }
        this.employees[this.selectedEmpIndex] = { ...this.selectedEmployee }
        localStorage.setItem(this.key, JSON.stringify(this.employees));
      }
    },
    generateQRCode() {
      this.qrcodeContent = buildQrCodeContent({
        bankId: this.selectedEmployee.bankCode,
        accountNo: this.selectedEmployee.bankAccount,
        amount: this.selectedEmployee.amount.toString(),
        message: removeAccents(this.selectedEmployee.message),
      });
      console.log(this.selectedEmployee)
      this.generateQRCodeImage(this.qrcodeContent)
    },
    generateQRCodeImage(content) {
      this.$nextTick(() => {
        const canvas = document.getElementById("qrcode");

        if (!canvas) {
          console.error("Canvas element not found!");
          return;
        }

        const context = canvas.getContext("2d");
        context.clearRect(0, 0, canvas.width, canvas.height);

        QRCode.toCanvas(canvas, content, { width: 200 }, function (error) {
          if (error) {
            console.error("Error generating QR code:", error);
          } else {
            console.log("QR code successfully generated!");
          }
        });
      });
    },
  },
  created() {
    this.key = '202505'
    const emps = localStorage.getItem(this.key);
    EMPLOYEES.forEach(emp => {
      emp.id = `${emp.bankCode}-${emp.bankAccount}`
      this.originalData[emp.id] = emp
    })
    if (!emps) {
      this.employees = EMPLOYEES
    } else {
      this.employees = JSON.parse(emps)
    }
  }
};
</script>

<style scoped>
.container {
  display: flex;
  gap: 20px;
}

.emp-list {
  flex: 3;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
  height: 100vh;
  overflow-y: scroll;
}

.emp-detail {
  flex: 2;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.table th,
.table td {
  padding: 10px;
  text-align: left;
  border: 1px solid #ddd;
}

.table th {
  background-color: #f0f0f0;
  font-weight: bold;
}

.emp-row:hover {
  background-color: #f1f1f1;
  cursor: pointer;
}

.emp-row-selected {
  background-color: lightgray;
}

.toggle-button {
  padding: 5px 10px;
  color: #fff;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.toggle-button:hover {
  background-color: #45a049;
}

.detail-field label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.detail-value {
  display: inline-block;
  padding: 8px;
  background-color: #eee;
  border-radius: 4px;
}

.input {
  text-align: right;
}

.generate-button {
  margin-top: 15px;
  padding: 10px 20px;
  color: #fff;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.detail-field {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.detail-field label {
  flex: 0 0 150px;
  font-weight: bold;
}

.select,
.input {
  width: 100%;
  flex: 1;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.generate-button:hover {
  background-color: #45a049;
}

.qrcode-container {
  margin-top: 20px;
  text-align: center;
}

.qrcode-container canvas {
  margin-top: 10px;
  border: 1px solid #ddd;
  padding: 10px;
  background-color: #fff;
}

/* Định dạng thông báo đổi dữ liệu */
.modified-warning {
  background-color: #ffd2d2;
  color: #d8000c;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  font-weight: bold;
}

.reset-button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 5px;
}

.reset-button:hover {
  background-color: #45a049;
}

/* Đánh dấu dòng nhân viên có sửa đổi */
.emp-modified {
  background-color: #fff3cd;
}

.summary {
  text-align: start;
}
</style>
